import {React, useState} from 'react';
import './App.css';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import RestoreIcon from '@material-ui/icons/Restore';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import CheckIcon from '@material-ui/icons/Check';
import universe from "./universe.jpg";
import useSound from 'use-sound';
import bip from './bip.wav';
import CloseIcon from '@material-ui/icons/Close';

let timerJob = undefined;
let timerRunning = false;
let roundTotalTimer = 30;
let roundTimer = 30;

let bonusTimerJob = undefined;
let bonusRoundTimer = 60;
let correctAnswers = 0;
let bonusTimerRunning = false;

function App() {


  const [timerIsRunning, setTimerRunning] = useState(false);
  const [round, setRound] = useState(1);
  const [timer, setTimer] = useState(30);
  const [timerPercentage, setTimerPercentage] = useState(100);

  //bonus
  const [bonusTimerPercentage, setBonusTimerPercentage] = useState(100);
  const [extraJokerLeft1, setExtraJokerLeft1] = useState(0);
  const [extraJokerLeft2, setExtraJokerLeft2] = useState(0);
  const [bonusTimer, setBonusTimer] = useState(60);
  const [bonusRound, setBonusRound] = useState(true);

  const [playBip] = useSound(bip);

  const incrementRound = () => {
    if(!timerRunning && round < 12){
      setRound(round + 1);
      updateTimer(round + 1);
    }
  }

  const decrementRound = () => {
    if(!timerRunning && round > 1){
      setRound(round - 1);
      updateTimer(round - 1);
    }
  }

  const updateTimer = (nextRound) => {
    roundTotalTimer = 30;
    if(nextRound > 4){
      roundTotalTimer = 40;
    }
    if(nextRound > 8){
      roundTotalTimer = 50;
    }
    roundTimer = roundTotalTimer;
    setTimer(roundTotalTimer);
    setTimerPercentage(100);
  }

  const onTimerStart = () => {
    if(typeof timerJob !== "undefined"){
      clearInterval(timerJob);
    }
    timerJob = setInterval(() => {
      if(roundTimer - 1 >= 0){
        roundTimer -= 1;
        setTimer(roundTimer);
        setTimerPercentage(parseInt((roundTimer / roundTotalTimer) * 100));
        if(roundTimer - 1 === 2){
          playBip();
        }
      } else {
        clearInterval(timerJob);
        timerRunning = false;
        setTimerRunning(timerRunning);
        updateTimer(round);
      }
    }, 1000);
    timerRunning = true;
    setTimerRunning(timerRunning);
  }

  const restartTimer = () => {
    if(typeof timerJob !== "undefined"){
      clearInterval(timerJob);
    }
    timerRunning = false;
    setTimerRunning(timerRunning);
    updateTimer(round);
  }

  const useJoker = () => {
    if(typeof timerJob !== "undefined"){
      clearInterval(timerJob);
    }
    timerRunning = false;
    setTimerRunning(timerRunning);
    roundTotalTimer += 15;
    roundTimer += 15;
    setTimer(roundTimer);
    setTimerPercentage(parseInt((roundTimer / roundTotalTimer) * 100));
  }

  const onBonusTimerStart = () => {
    if(typeof bonusTimerJob === "undefined" && correctAnswers < 12){
      bonusTimerJob = setInterval(() => {
        if(bonusRoundTimer - 1 >= 0){
          bonusRoundTimer -= 1;
          setBonusTimer(bonusRoundTimer);
          setBonusTimerPercentage(parseInt((bonusRoundTimer / 60) * 100));
          if(bonusRoundTimer - 1 === 2){
            playBip();
          }
        } else {
          bonusTimerRunning = false;
          clearInterval(bonusTimerJob);
          bonusRoundTimer = 60;
          setBonusTimer(bonusRoundTimer);
          setBonusTimerPercentage(parseInt((bonusRoundTimer / 60) * 100));
        }
      }, 1000);
      bonusTimerRunning = true;
    }
  }

  const onCorrectAnswer = () => {
    if(bonusTimerRunning && correctAnswers < 11){
      correctAnswers += 1;
      if(correctAnswers < 6){
        setExtraJokerLeft1(parseInt((correctAnswers / 5) * 100));
      } else {
        setExtraJokerLeft2(parseInt(((correctAnswers - 5) / 5) * 100));
      }
      if(correctAnswers === 10){
        bonusTimerRunning = false;
        clearInterval(bonusTimerJob);
      }
    }
  }

  const closeBonusPopup = () => {
    setBonusRound(false);
    bonusTimerRunning = false;
    clearInterval(bonusTimerJob);
    setExtraJokerLeft1(0);
    setExtraJokerLeft2(0);
    correctAnswers = 0;
    bonusRoundTimer = 60;
    setBonusTimerPercentage(100);
  }

  return (
    <div className="App" style={{
      backgroundImage: 'url('+universe+')',
      backgroundSize: "cover",
      height: "100vh",
      color: "#f5f5f5"
    }} >
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Grid container spacing={3} className="RoundContainer">
              <Grid item xs={3} sm={3} md={4} lg={5} className="PrevRoundContainer">
                <Fab className="RoundButton" aria-label="delete" size="small" onClick={decrementRound} disabled={timerIsRunning}>
                  <ArrowBackIcon className="ButtonIcon"/>
                </Fab>
              </Grid>
              <Grid item xs={6} sm={6} md={4} lg={2}>
              <Typography className="RoundLabel" variant="h4" component="h4">
                Ronda {round}
              </Typography>
              </Grid>
              <Grid item xs={3} sm={3} md={4} lg={5} className="NextRoundContainer">
                <Fab className="RoundButton" aria-label="delete" size="small" onClick={incrementRound} disabled={timerIsRunning}>
                  <ArrowForwardIcon className="ButtonIcon"/>
                </Fab>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={12} className="TimerContainer">
            <CircularProgress variant="determinate" value={timerPercentage} className="TimerProgress"/>
            <Typography className="TimerLabel" variant="h3" component="h3" gutterBottom>
                {timer}
              </Typography>
          </Grid>
          <Grid item xs={12} className="TimerActionsContainer">
                <Fab aria-label="delete" size="small" className="TimeButton" onClick={onTimerStart}>
                  <PlayArrowIcon className="ButtonIcon"/>
                </Fab>
                <Fab aria-label="delete" size="small" className="TimeButton" onClick={restartTimer}>
                  <RestoreIcon className="ButtonIcon"/>
                </Fab>
          </Grid>
          <Grid item xs={12} className="TimerActionsContainer">
            <Fab variant="extended" className="JokerButton" onClick={useJoker}>
              <div className="JokerTop"></div>
              JOKER
              <div className="JokerBottom"></div>
            </Fab>
          </Grid>
          <Grid item xs={12} className="BonusRoundContainer">
            <Fab variant="extended" className="BonusRoundButton" onClick={() => setBonusRound(true)} disabled={timerIsRunning}>
              Ronda Bónus
            </Fab>
          </Grid>
        </Grid>
        <Dialog
          disableBackdropClick
          open={bonusRound}
          onClose={() => setBonusRound(false)}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
          className="BonusRoundContainer"
        >
          <Fab aria-label="delete" size="small" className="CloseBonusPopup" onClick={closeBonusPopup}>
                  <CloseIcon className="ButtonIcon"/>
          </Fab>
          <DialogTitle id="alert-dialog-title" className="BonusRoundTitle">
                Ronda Bónus
          </DialogTitle>
          <DialogContent className="BonusRoundContent">
            <Grid container spacing={3}>
              <Grid item xs={12} className="BonusRoundLabels">
                <div className="JokerAwardedContainer">
                  <Fab variant="extended" className="JokerAwarded">
                    <div className="JokerTop"></div>
                      JOKER
                    <div className="JokerBottom"></div>
                    <div className="JokerAwardedOverflow" style={{left:extraJokerLeft2 + "%"}}></div>
                  </Fab>
                </div>
                <div className="JokerAwardedContainer" style={{marginTop: 20}}>
                  <Fab variant="extended" className="JokerAwarded" disabled>
                    <div className="JokerTop"></div>
                      JOKER
                    <div className="JokerBottom"></div>
                    <div className="JokerAwardedOverflow" style={{left:extraJokerLeft1 + "%"}}></div>
                  </Fab>
                </div>
              </Grid>
              <Grid item xs={12} className="BonusTimerContainer">
                <CircularProgress variant="determinate" value={bonusTimerPercentage} className="BonusRoundTimerProgress"/>
                <Typography className="BonusRoundTimerLabel" variant="h3" component="h3" gutterBottom>
                  {bonusTimer}
                </Typography>
              </Grid>
              <Grid item xs={12} className="TimerActionsContainer">
                <Fab aria-label="delete" size="small" className="TimeButton" onClick={onBonusTimerStart}>
                    <PlayArrowIcon className="ButtonIcon"/>
                </Fab>
              </Grid>
              <Grid item xs={12} className="BonusTimerActionsContainer">
                <Fab variant="extended" className="BonusTimerCorrectButton" onClick={onCorrectAnswer}>
                  <CheckIcon className="BonusTimerCorrectButtonIcon"/>
                  Correto
                </Fab>
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
           
          </DialogActions>
        </Dialog>
    </div>
  );
}

export default App;
